import React, { useState, createContext } from "react";
import { useLocation, Link, Outlet } from "react-router-dom";
import Title from "../components/Title";

export const themcontext = createContext();
const Catalog = () => {
  const category = [
    { display: "Tri cy", path: "/catalog" },
    { display: "Gi qu", path: "/catalog/gift" },
    { display: "Thc phm", path: "/catalog/food" },
    { display: "Bnh ko", path: "/catalog/confectionery" },
  ];
  const { pathname } = useLocation();
  const active = category.findIndex((e) => e.path === pathname);

  return (
    <Title title="Sn phm">
      <div className="catalog">
        <div className="catalog__category">
          {category.map((item, index) => (
            <Link to={item.path} key={index}>
              <div
                className={`catalog__category__item ${
                  index === active ? "active" : ""
                }`}
              >
                <span className="catalog__category__item__name">
                  {item.display}
                </span>
              </div>
            </Link>
          ))}
        </div>
        <div className="catalog__container">
          <div className="catalog__container__content">
            <Outlet />
          </div>
        </div>
      </div>
    </Title>
  );
};

export default Catalog;
