import React, { useState, useEffect } from "react";
import Title from "../components/Title";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import productData from "../fake-Data/products";
import Food from "../fake-Data/food";
import NumberCommas from "../sanzai/NumberCommas";
import Button from "../components/Button";
import CartItems from "../components/CartItems";

const Cart = () => {
  const cartItems = useSelector((state) => state.cartItems.value);

  const [cartProduct, setCartProduct] = useState([]);
  const [totalProduct, setTotalProduct] = useState(0);
  const [totalPrice, setTotalPrice] = useState(0);

  useEffect(() => {
    setCartProduct(Food.getCartItem(cartItems));
    setTotalProduct(
      cartItems.reduce((total, item) => total + Number(item.quantity), 0)
    );
    setTotalPrice(
      cartItems.reduce(
        (total, item) => total + Number(item.quantity) * Number(item.price),
        0
      )
    );
  }, [cartItems]);

  return (
    <Title title="Gi hng">
      <div className="cart">
        <div className="cart__info">
          <div className="cart__info__title">
            <p>
              Bn ang c <strong>{totalProduct}</strong> sn phm trong gi
              hng.
            </p>
            <div className="cart__info__title__price">
              <span>Thnh tin:</span>
              <span>{NumberCommas(totalPrice)}</span>
            </div>
          </div>
          <div className="cart__info__btn">
            <Button size="block">t hng</Button>
            <Link to="/catalog">
              <Button size="block">Tip tc mua hng</Button>
            </Link>
          </div>
        </div>
        <div className="cart__list">
          {cartProduct.map((item, index) => (
            <CartItems key={index} item={item} />
          ))}
        </div>
      </div>
    </Title>
  );
};

export default Cart;
