import React from "react";
import { Link } from "react-router-dom";
import Grid from "./Grid";

const Footer = () => {
  const footer = [
    {
      display: "Gii thiu",
      path: "/about",
    },
    {
      display: "Lin h",
      path: "/about",
    },
   
    {
      display: "H tr",
      path: "/about",
    },
    {
      display: "tin tc",
      path: "/about",
    },
  ];
  const footerCustom = [
    {
      display: "Chnh sch bo hnh",
      path: "/about",
    },
    {
      display: "H thng ca hng",
      path: "/about",
    },
    {
      display: "Chnh sch h tr",
      path: "/about",
    },
  ];
  const footerAbcfruit=[{
    display: "Tiu ch ca hng",
    path: "/about",
    content: `Phc v khch hng hi lng l nim vui ca chng ti. "ABCfruit" l An ton - Bo m - Cht lng c u tin hng u cho ngi tiu dng, chng ti s m n nhng sn phm cht lng nht cho khch hng. `
  },

  ]
  return (
    <footer className="footer">
      <div className="container">
        <Grid col={4} mdcol={2} smcol={1} gap={10}>
          <div>
            <div className="footer__title">Tng i h tr</div>
            <div className="footer__content">
              <p>
                {" "}
                Lin h t hng <strong>0123456789</strong>
              </p>
              <p>
                {" "}
                khiu ni sn phm <strong>0123456789</strong>
              </p>
              <p>
                {" "}
                nh gi cht lng <strong>0123456789</strong>
              </p>
              <p>
                {" "}
                Gp  dch v <strong>0123456789</strong>
              </p>
            </div>
          </div>
          <div>
            <div className="footer__title">ABCfruit</div>
            <div className="footer__content">
              {footer.map((item, index) => (
                <p key={index}>
                  <Link to={item.path}>{item.display}</Link>
                </p>
              ))}
            </div>
          </div>
          <div>
            <div className="footer__title">Khch hng </div>
            <div className="footer__content">
              {footerCustom.map((item, index) => (
                <p key={index}>
                  <Link to={item.path}>{item.display}</Link>
                </p>
              ))}
            </div>
          </div>
          <div>
            <div className="footer__title">Tiu ch ca hng </div>
            <div className="footer__content">
              {footerAbcfruit.map((item, index) => (
                <p key={index}>
                 {item.content}
                </p>
              ))}
            </div>
          </div>
        </Grid>
      </div>
    </footer>
  );
};

export default Footer;
