import React, { useState, useEffect } from "react";
import Title from "../components/Title";
import Section, { SectionContent } from "../components/Section";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { setId } from "../redux/idUser/idUser";
const Registration = () => {
  const idUsers = useSelector((state) => state.idUsers.value);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [user, setUser] = useState({
    name: "",
    password: "",
    email: "",
    gender: "male",
    agree: false,
  });
  const [errName, setErrName] = useState("");
  const [errPassword, setErrPassword] = useState("");
  const [errEmail, setErrEmail] = useState("");
  const handleClickForm = (event) => {
    const userName = idUsers.find((e) => e.name === user.name);
    console.log(userName);
    if (user.name) {
      if (userName === undefined) {
        dispatch(setId(user));
        navigate("/login");
      } else {
        console.log("ten da ton tai");
        setErrName("Tn  tn ti");
      }
    } else {
      console.log("chua vao ");
      setErrName("Cha nhp tn");
      setErrPassword("Cha nhp mt khu");
      setErrEmail("Cha nhp email");
    }
  };
  const handleClose = () => {
    navigate("/");
  };
  return (
    <Title title="ng k">
      <div className="container__modal">
        <div className="container__logregi">
          <div className="container__logregi__close" onClick={handleClose}>
            <i className="bx bx-x"></i>
          </div>
          <h1 className="container__logregi__title">ABCfruit</h1>
          <div className="container__logregi__user">
            <div className="container__logregi__user__name">
              <label htmlFor="username">Tn ngi dng:</label>
              <input
                type="text"
                name="username"
                id="username"
                onChange={(e) => setUser({ ...user, name: e.target.value })}
              ></input>
              <p>{user.name ? "" : errName}</p>
            </div>
            <div className="container__logregi__user__password">
              <label htmlFor="password">Mt khu:</label>
              <input
                type="password"
                name="password"
                id="password"
                onChange={(e) => setUser({ ...user, password: e.target.value })}
              ></input>
              <p>{user.password !== "" ? "" : errPassword}</p>
            </div>
            <div className="container__logregi__user__email">
              <label htmlFor="username">Email ngi dng:</label>
              <input
                type="email"
                name="email"
                id="email"
                onChange={(e) => setUser({ ...user, email: e.target.value })}
              ></input>
              <p> {user.email !== "" ? "" : errEmail}</p>
            </div>
          </div>
          <div className="container__logregi__gender">
            <input
              type="radio"
              name="gender"
              value="male"
              checked={user.gender === "male"}
              onChange={(e) => setUser({ ...user, gender: e.target.value })}
            />
            Nam
            <input
              type="radio"
              name="gender"
              value="female"
              checked={user.gender === "female"}
              onChange={(e) => setUser({ ...user, gender: e.target.value })}
            />
            N
          </div>
          <div className="container__logregi__accept">
            <input
              type="checkbox"
              name="agree"
              checked={user.agree}
              onChange={(e) => setUser({ ...user, agree: e.target.checked })}
            />
            ng  vi cc iu khong
          </div>
          <div className="container__logregi__btn">
            <button type="submit" onClick={handleClickForm}>
              ng k
            </button>
            <Link to="/login">
              <p className="gotologin">ng nhp</p>
            </Link>
          </div>
        </div>
      </div>
    </Title>
  );
};

export default Registration;
